@use "sass:math";

////////////////////
// 
////////////////////
// 
$inner: 1100px;
// 

// 
$padding-pc: 25px;
$padding-sp: 20px;
// PCPC25px


////////////////////
// 
////////////////////

$base-font-family: "";
$second-font-family: "";
// 

body {
  font-family: $base-font-family;
}

// 
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 
////////////////////

//
$black: #111;
$white: #fff;


////////////////////
// 
////////////////////

// 
$breakpoints: (
    min: 0,
    xxs: 375px,
    xs: 414px,
    sm: 576px,
    md: 768px,
    lg: 1024px,
    xl: 1280px,
    xxl: 1480px,
) !default;

//
$mediaquerys: (
  min: "screen and (min-width: #{map-get($breakpoints,'min')}px)",
  xxs: "screen and (min-width: #{map-get($breakpoints,'xxs')}px)",
  xs: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
  xxl: "screen and (min-width: #{map-get($breakpoints,'xxl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 
////////////////////

//768px
.u-desktop {
  display: none;
  @include mq("md") {
    display: block;
  }
}

//
.u-mobile {
  @include mq("md") {
    display: none;
  }
}

// 
$space: 8;

$space-range: 30;

// Grid Column
$column: 12;

// Grid Row
$row: 6;

// 
$scroll-bar: 0;

// 
$prefix: "l";
// Content
$content-widths: (
    xs: 600,
    sm: 640,
    md: 768,
    lg: 1024,
    l: 1080,
    xl: 1280,
    xxl: 1440,
) !default;

// 
$comps: (
    sm: 375,
    md: 768,
    lg: 1440,
) !default;

:root {
  --ol-grid-columns: 3;
}